@use "../base/settings" as *;

.movies-container {
  display: flex;
  flex-wrap: wrap;
  gap: 1.7rem;
  justify-content: center;

  .individual-movie {
    display: flex;
    flex-direction: column;
  }

  article.individual-movie-container {
    position: relative;
    display: inline-block;

    img {
      width: 140px;
      border-radius: 15px;
      display: block;
      height: auto;
      box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.3);
    }

    .movie-details {
      display: flex;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: #3a266fe2;
      border-radius: 15px;
      color: #fff;
      padding: 1rem;
      flex-direction: column;
      justify-content: flex-end;
      align-items: center;
      opacity: 0;

      h2 {
        font-size: 1rem;
        text-align: center;
        font-weight: 600;
        color: $fontWhite;
        margin-bottom: 0.2rem;
      }

      .movie-details-year-rating {
        display: flex;
        margin-bottom: 0.2rem;
        p {
          padding: 0.3rem;
        }
      }

      p {
        display: none;
      }

      &:active,
      &:hover {
        opacity: 1;
      }
    }
  }
}

.error-message {
  color: red;
  font-size: 1.2rem;
  font-weight: 600;
  text-align: center;
  margin-top: 2rem;
}

.more-info-button {
  background-color: $backgroundColor;
  padding: 0.4rem 1.2rem;
  border: none;
  border-radius: 10px;
  color: $fontWhite;
  font-family: $font;
  font-size: 0.75rem;
  margin-top: 0.5rem;
}

/////////////////// change breakpoint! ///////////////////
@media (min-width: 600px) {
  .movies-container {
    width: 90%;
    margin: 0 auto;
  }
}

@media (min-width: 800px) {
  .movies-container article.individual-movie-container {
    img {
      width: 180px;
    }

    .movie-details p {
      display: block;
      font-size: 0.7rem;
      color: $fontWhite;
      font-weight: 400;
      text-align: center;
    }
  }
}
